import * as React from 'react';
import { IconButton, Table, TableBody, TableRow, TableCell, Collapse, Typography } from '@mui/material';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

function getFieldValue(field, data) {
  let value = "foo bar";
  let realValue = findValueForKey(data.processed, field) || findValueForKey(data.raw, field) || "N/A";
  console.log("realValue", realValue);
  return realValue.toString();
}

function findValueForKey(obj, key) {
  let value = "";

  for (let k in obj) {
    if (k === key) {
      value = obj[k];
    } else if (!value && typeof obj[k] === 'object') {
      value = findValueForKey(obj[k], key);
    }
  }

  return value
}


export default function RecordSection({recordData, section, fieldList}) {
  const [open, setOpen] = React.useState(true);

  return (
    
    <React.Fragment>
      <TableRow sx={{ '& > *': { borderBottom: 'unset' } }} onClick={() => setOpen(!open)}>
        <TableCell style={{ width: "80%", paddingBottom: 8, paddingTop: 8}}>
          <Typography variant="h6" component="span">
            {section}
          </Typography>
        </TableCell>
        <TableCell align="right" style={{ width: "0%", paddingBottom: 8, paddingTop: 8 }}>
          {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={2}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Table aria-label="collapsible table">
              <TableBody>
              {fieldList.map((field) => (
                <TableRow key={field}>
                  <TableCell style={{ width: "25%", paddingLeft: 0, paddingBottom: 0, paddingTop: 0 }} colSpan={6}>{field}</TableCell>
                  <TableCell style={{ width: "75%", paddingLeft: 0, paddingBottom: 0, paddingTop: 0 }} colSpan={6}>{getFieldValue(field, recordData)}</TableCell>
                </TableRow>
              ))}
              </TableBody>
            </Table>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}